@mixin navbar-responsive() {
    &.menu-on-left .navbar-translate {
        flex-direction: row-reverse;
    }

    .navbar {
        .navbar-nav {
            margin-top: 53px;
            position: relative;
            max-height: calc(100vh - 75px);
            min-height: 100%;
            overflow: auto;

            &.navbar-logo {
                top: 0;
                height: 53px;
            }
        }

        .dropdown.show .dropdown-menu,
        .dropdown .dropdown-menu {
            background-color: transparent;
            border: 0;
            transition: none;
            -webkit-box-shadow: none;
            box-shadow: none;
            width: auto;
            margin: 0 1rem;
            margin-bottom: 15px;
            padding-top: 0;
            height: 125px;
            overflow-y: scroll;

            &:before {
                display: none;
            }
        }

        .dropdown {
            .dropdown-item {
                padding-left: 2.5rem;
            }

            .dropdown-menu {
                display: none;
            }

            &.show .dropdown-menu {
                display: block;
            }
        }

        .dropdown-menu .dropdown-item:focus,
        .dropdown-menu .dropdown-item:hover {
            color: $white-color;
            border-radius: $border-radius-small;
        }

        .navbar-translate {
            width: 100%;
            position: relative;
            display: flex;
            -ms-flex-pack: justify !important;
            justify-content: space-between !important;
            -ms-flex-align: center;
            align-items: center;
            @include transform-translate-x(0px);
            @include transition (0.50s, cubic-bezier(0.685, 0.0473, 0.346, 1));
        }

        .navbar-toggler-bar {
            display: block;
            position: relative;
            width: 22px;
            height: 1px;
            border-radius: 1px;
            background: $navbar-item;

            &+.navbar-toggler-bar {
                margin-top: 7px;
            }

            &.bar2 {
                width: 17px;
                transition: width .2s linear;
            }
        }

        &.bg-white:not(.navbar-transparent) .navbar-toggler-bar {
            background: $default-color;
        }

    }

    // .bar1,
    // .bar2,
    // .bar3 {
    //   outline: 1px solid transparent;
    // }
    // .bar1 {
    //   top: 0px;
    //   @include bar-animation($topbar-back);
    // }
    // .bar2 {
    //   opacity: 1;
    // }
    // .bar3 {
    //   bottom: 0px;
    //   @include bar-animation($bottombar-back);
    // }
    // .toggled .bar1 {
    //   top: 6px;
    //   @include bar-animation($topbar-x);
    // }
    // .toggled .bar2 {
    //   opacity: 0;
    // }
    // .toggled .bar3 {
    //   bottom: 6px;
    //   @include bar-animation($bottombar-x);
    // }

    .top-bar {
        transform: rotate(0);
    }

    .middle-bar {
        opacity: 1;
    }

    .bottom-bar {
        transform: rotate(0);
    }

    .top-bar,
    .middle-bar,
    .bottom-bar {
        transition: all 0.2s;
    }



    .toggled {
        .top-bar {
            transform: rotate(45deg);
            transform-origin: 0%;
        }

        .bottom-bar {
            transform: rotate(-45deg);
            transform-origin: 0%;
        }

        .middle-bar {
            opacity: 0;
        }
    }



    //
    // @include topbar-x-rotation();
    // @include topbar-back-rotation();
    // @include bottombar-x-rotation();
    // @include bottombar-back-rotation();
