    //   0% {opacity: 0;}
    //   100% {opacity: 1;}
    // }
}

@mixin navbar-collapse() {
    .navbar-collapse {
        position: fixed;
        display: block !important;
        top: 0;
        height: 100% !important;
        width: 300px;
        right: 0;
        z-index: 1032;
        visibility: visible;
        background-color: #999;
        overflow-y: visible;
        border-top: none;
        text-align: left;

        max-height: none !important;

        @include transform-translate-x(300px);
        @include transition (0.50s, cubic-bezier(0.685, 0.0473, 0.346, 1));

        &:before {
            background: $brand-primary;
            /* For browsers that do not support gradients */
            background: -webkit-linear-gradient($brand-primary 0%, #000 80%);
            /* For Safari 5.1 to 6.0 */
            background: -o-linear-gradient($brand-primary 0%, #000 80%);
            /* For Opera 11.1 to 12.0 */
            background: -moz-linear-gradient($brand-primary 0%, #000 80%);
            /* For Firefox 3.6 to 15 */
            background: linear-gradient($brand-primary 0%, #000 80%);
            /* Standard syntax (must be last) */
            @include opacity(.76);

            display: block;
            content: "";
            position: absolute;
            width: 100%;
            height: 100%;
            top: 0;
            left: 0;
            z-index: -1;
        }

        .navbar-nav:not(.navbar-logo) {
            .nav-link {
                margin: 0 1rem;
                margin-top: 0.3125rem;

                &:not(.btn) {
                    color: $navbar-item !important;
                    // width: fit-content;
                }
            }
        }

        .dropdown-menu .dropdown-item {
            color: $white-color;
        }
    }

    @include navbar-responsive();


    [class*="navbar-expand-"] .navbar-collapse {
        width: 180px;
    }

    .wrapper {
        @include transition (0.50s, cubic-bezier(0.685, 0.0473, 0.346, 1));
    }

    #bodyClick {
        height: 100%;
        width: 100%;
        position: fixed;
        opacity: 1;
        top: 0;
        left: auto;
        right: 300px;
        content: "";
        z-index: 9999;
        overflow-x: hidden;
        background-color: transparent;
        @include transition (0.50s, cubic-bezier(0.685, 0.0473, 0.346, 1));
    }

}